$color-base: #07b1e8;
$color-txt: #23272c;
$color-light-grey: #e7ebf2;
$color-grey: #d3dbeb;
$color-mid-grey: #ecedef;
$color-blue: #203653;
$color-dark-blue: #1c2d43;
%bg-cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center; }

%bg-auto {
	background-repeat: no-repeat;
	background-size: auto;
	background-position: center center; }

%bg-contain {
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center center; }
%b {
	border: 1px solid red; }
%flex-wrap {
	display: flex;
	flex-wrap: wrap; }
%flex-col {
	display: flex;
	flex-direction: column; }

.scroll-wrapper {
	overflow: hidden !important;
	padding: 0 !important;
	position: relative;
	> .scroll-content {
		border: none !important;
		box-sizing: content-box !important;
		height: auto;
		left: 0;
		margin: 0;
		max-height: none;
		max-width: none !important;
		overflow: scroll !important;
		padding: 0;
		position: relative !important;
		top: 0;
		width: auto !important;
		&::-webkit-scrollbar {
			height: 0;
			width: 0; } } }

.scroll-element {
	display: none;
	box-sizing: content-box;
	div {
		box-sizing: content-box; }
	&.scroll-x.scroll-scrollx_visible, &.scroll-y.scroll-scrolly_visible {
		display: block; }
	.scroll-bar, .scroll-arrow {
		cursor: default; } }

.scroll-textarea {
	border: 1px solid #cccccc;
	border-top-color: #999999;
	> .scroll-content {
		overflow: hidden !important;
		> textarea {
			border: none !important;
			box-sizing: border-box;
			height: 100% !important;
			margin: 0;
			max-height: none !important;
			max-width: none !important;
			overflow: scroll !important;
			outline: none;
			padding: 2px;
			position: relative !important;
			top: 0;
			width: 100% !important;
			&::-webkit-scrollbar {
				height: 0;
				width: 0; } } } }



.scrollbar-inner > .scroll-element {
	border: none;
	margin: 0;
	padding: 0;
	position: absolute;
	z-index: 10;
	div {
		border: none;
		margin: 0;
		padding: 0;
		position: absolute;
		z-index: 10;
		display: block;
		height: 100%;
		left: 0;
		top: 0;
		width: 100%; }
	&.scroll-x {
		bottom: 2px;
		height: 8px;
		left: 0;
		width: 100%; }
	&.scroll-y {
		height: 100%;
		right: 0;
		top: 0;
		width: 8px; }
	.scroll-element_outer {
		overflow: hidden;
		-webkit-border-radius: 0;
		-moz-border-radius: 0;
		border-radius: 0; }
	.scroll-element_track, .scroll-bar {
		-webkit-border-radius: 0;
		-moz-border-radius: 0;
		border-radius: 0; }
	.scroll-element_track, .scroll-bar {
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
		filter: alpha(opacity = 100);
		opacity: 1; }
	.scroll-element_track {
		background-color: #e0e0e0; }
	.scroll-bar {
		background-color: red; }
	&:hover .scroll-bar, &.scroll-draggable .scroll-bar {
		background-color: #919191; }
	&.scroll-x.scroll-scrolly_visible .scroll-element_track {
		left: -12px; }
	&.scroll-y.scroll-scrollx_visible .scroll-element_track {
		top: -12px; }
	&.scroll-x.scroll-scrolly_visible .scroll-element_size {
		left: -12px; }
	&.scroll-y.scroll-scrollx_visible .scroll-element_size {
		top: -12px; } }

