@import "variables";
@import "functions";

@media screen and (max-width: 1366px) {
	html {
		font-size: 15px; }
	h1 {
		font-size: toRem(48px); }
	.breadcrumbs {
		padding: 12px 0; }

	.kinds-of {
		&-forms {
			&__item {
				padding: 10px;
				width: 250px;
				span {
					flex: 0 0 auto; } } } }

	.sidebar {
		width: 200px; }

	.breadcrumbs {
		ul {
			li,a {
				font-size: 1rem; } } }
	.front-page {
		height: auto; } }


@media screen and (max-width: 1170px) {
	.production-list {
		margin-left: 0;
		margin-right: 0; }
	.product-list {
		margin-left: 0;
		margin-right: 0;
		justify-content: center; } }

@media screen and (max-width: 1024px) {
	h1 {
		font-size: toRem(40px); }
	.main-wrap {
		position: relative;
		overflow: hidden; }
	.sidebar {
		transform: translateX(-120%);
		width: auto;
		z-index: 40;
		position: absolute;
		top: 0;
		left: 0;
		background-color: #fff;
		padding: 15px 25px;
		padding-left: 10px;
		transition: .3s linear all;
		box-shadow: 15px 15px 25px 10px rgba(0,0,0,0.2);
		&.active {
			transform: translateX(0);
			transition: .3s linear all; }
		&.fixed {
			position: fixed;
			top: 15px;
			left: 15px; } }
	.contacts__tel {
		font-size: toRem(18px);
		padding-left: 25px; }
	.header {
		.main-menu {
			margin-left: 10px;
			a {
				font-size: 1rem;
				padding-bottom: 7px; } } }
	.kinds-of-forms {
		&__item {
			width: 200px;
			height: 60px;
			span {
				max-width: 50px; } } }
	.sizes {
		.kinds-of-forms {
			&__item {
				width: 160px;
				height: 30px;
				padding: 0 10px; } } }
	.set-custom-props {
		.input {
			&-group {
				justify-content: center; }
			&-field {
				margin-bottom: 25px; } } }
	.footer {
		.contacts {
			justify-content: center; }
		&__bottom {
			.flex-wrap {
				justify-content: center; } } }
	.section-production {
		.production-list {
			max-width: 100%;
			justify-content: center; } }
	.contact-info-list {
		margin-left: auto;
		margin-right: auto;
		li {
			max-width: 100%;
			margin: 0 15px 30px;
			flex: 1 1 200px; } }
	.map {
		flex-direction: column;
		&-wrap {
			height: 600px;
			flex: none; } }
	.production-list {
		justify-content: center;
		&__ico {
			svg {
				width: 60px;
				height: 60px; } }
		a {
			text-align: center; } }
	.flex-table {
		.table-row {
			.table-cell {
				&:first-of-type {
					flex: 1 1 33%;
					padding-left: 5px; } } } }
	.product-table {
		.table-row {
			.table-cell {
				padding: 5px;
				&:first-of-type {
					padding-left: 5px; } } } }
	.feedback .section-head h2 {
		font-size: 1.5rem; } }





@media screen and (max-width: 768px) {
	html {
		font-size: 14px; }
	h2 {
		font-size: 1.4rem; }
	h3 {
		font-size: 1.3rem; }

	.header {
		.main-menu {
			transform: translateX(-120%);
			transition: .3s linear all;
			&.active {
				transform: translateX(0);
				transition: .3s linear all; } } }
	.header {
		position: relative;
		&-abs {
			position: absolute; }
		&-wrap {
			align-items: center;
			.main-menu {
				margin-left: 0;
				position: fixed;
				z-index: 100;
				top: 0;
				min-height: 100vh;
				left: 0;
				padding: 15px 25px;
				background-color: #fff;
				box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
				ul {
					flex-direction: column;
					align-items: flex-start; }
				li {
					margin-bottom: 15px;
					&:after {
						display: none; } }
				a {
					font-size: toRem(24px); } } }
		&-abs {}
		.main-menu {
			ul {
				a {
					color: $color-txt; } } } }
	.header {
		&-wrap {
			flex-wrap: wrap; }
		.contacts {
			flex-direction: row;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			margin-right: 50px;
			a {
				&:not(:last-of-type) {
					margin-right: 15px;
					margin-bottom: 0; } } }
		&-nav {
			justify-content: space-between; } }
	.examples-of-work {
		.flex-wrap {
			justify-content: center; } }
	.feedback {
		.input {
			&-group {
				margin-left: 0;
				margin-right: 0; }
			&-field {
				margin-bottom: 25px;
				input {
					width: 100%; } } } }
	.footer {
		.logo {
			margin-bottom: 15px; }
		&__top {
			.flex-wrap {
				flex-direction: column; } } }
	.banner__text, .main-banner__text {
		.btn {
			width: 200px;
			height: auto;
			padding: 5px 20px; } }
	.btn {
		width: 200px;
		padding: 5px 15px;
		height: auto; }
	.section-production {
		.section-head {
			.btn {
				width: 170px;
				height: auto; } } }
	.section-production {
		.production-list {
			justify-content: center;
			li {
				flex: 1 1 250px;
				text-align: center;
				a {
					padding: 25px;
					width: 100%;
					height: 100%; } }
			&__ico {
				svg {
					max-width: 50px; } } }
		.section-head {
			margin-right: 0;
			.btn {
				margin-left: auto;
				margin-right: auto; } } }
	.section-services {
		padding-bottom: get-vw-x(60px, 768); }
	.services-list {
		margin-left: 0;
		margin-right: 0;
		justify-content: center; }
	.product-list {
		justify-content: center;
		h4 {
			text-align: center; } }
	.logo {
		width: 150px; }
	.product-table {
		overflow-x: scroll;
		width: calc(100vw - 30px);
		&__wrap {
			min-width: 670px; } } }








@media screen and (max-width: 640px) {
	h1 {
		font-size: toRem(36px); }
	h2 {
		font-size: 1.3rem; }
	h3 {
		font-size: 1.1rem; }
	.feedback {
		.input-field {
			width: 100%; } }
	.header .contacts {
		margin-right: 0;
		width: 100%;
		a {
			font-weight: 500; } }
	.popup {
		.input-field, .btn {
			width: 300px; }
		&__bottom {
			padding: 25px 15px; } }
	.popup {
		ul {
			width: 100%;
			max-width: calc(100vw - 30px	); }
		.set-custom-props {
			.input-field {
				margin-left: 0;
				margin-right: 0; } } } }

@media screen and (max-width: 480px) {
	html {
		font-size: 13px; }
	h1 {
		font-size: toRem(30px); }
	h2 {
		font-size: 1.2rem; }
	h3 {
		font-size: 1.1rem; }
	.clients-list {
		li {
			width: 120px;
			margin-bottom: 50px; } }
	.product-list {
		&>li {
			width: 100%;
			margin-top: 15px;
			margin-bottom: 15px; }
		.btns-wrap {
			justify-content: center; }
		.plus-minus-group {
			margin-left: auto;
			margin-right: auto; } }
	.current-price {
		text-align: center; }
	.logo {
		width: 120px; }
	.product {
		&__order {
			flex-direction: column;
			align-items: center; }
		&-list {
			>li {
				margin-left: 5px;
				margin-right: 5px; } } }
	.section-production {
		.production-list {
			&__ico {
				margin-bottom: 10px;
				svg {
					max-width: 35px; } } } }
	.services-item, .production-list li {
		margin-bottom: 15px; }
	.map {
		.form-wrap {
			padding: 40px 25px;
			.btn {
				margin-left: auto;
				margin-right: auto; } } }
	.production-list {
		a {
			padding: 25px 15px; } }
	.product {
		&__order {
			padding: 20px; }
		&-list {
			&__content {
				flex-wrap: wrap; }
			&__img, &__info {
				flex: 1 1 auto; } } }

	.kinds-of-forms {
		justify-content: center; }
	.feedback {
		.input-field {
			margin-left: auto;
			margin-right: auto; } } }





@media screen and (max-width: 380px) {
	h1 {
		font-size: toRem(26px); }
	.popup {
		width: calc(100vw - 30px);

		.input-field, .btn {
			width: 100%;
			margin-left: 0;
			margin-right: 0; }
		&__bottom {
			padding: 25px 15px; } }
	.custom-select {
		padding-left: 10px; } }

